/* Hero Section Styles */
.hero {
    background: linear-gradient(45deg, #0077b3, #004d66);
    color: white;
    text-align: center;
    padding: 100px 20px;
    border-radius: 10px;
}

.hero h1 {
    font-size: 3rem;
    margin-bottom: 15px;
    animation: fadeIn 1s ease-out;
}

.hero p {
    font-size: 1.2rem;
    margin-bottom: 30px;
    animation: fadeIn 1s ease-out;
}

.cta-button {
    background-color: #00bcd4;
    font-size: 1.2rem;
    padding: 15px 40px;
    border-radius: 5px;
    transition: background-color 0.3s ease;
}

.cta-button:hover {
    background-color: #008b9f;
}
