/* Navbar Container */
.navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem 2rem;
    background-color: #1c1c1c;
    color: white;
    transition: background-color 0.3s ease-in-out, box-shadow 0.3s ease-in-out;
    position: sticky;
    top: 0;
    z-index: 1000;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
}

/* Navbar Brand */
.navbar-brand h1 {
    font-family: 'Roboto', sans-serif;
    font-size: 2rem;
    font-weight: 700;
    color: #00bcd4;
    letter-spacing: 1px;
    transition: color 0.3s ease;
}

.navbar-brand h1:hover {
    color: #0097a7;
}

/* Navbar Links */
.navbar-links {
    display: flex;
    gap: 1.5rem;
}

.navbar-link {
    display: flex;
    align-items: center;
    text-decoration: none;
    color: white;
    font-size: 1.1rem;
    transition: color 0.3s ease, transform 0.3s ease;
}

.navbar-link:hover {
    color: #00bcd4;
    transform: scale(1.05);
}

.navbar-link svg {
    margin-right: 8px;
    font-size: 1.4rem;
}

/* Navbar Icons */
.navbar-icons {
    display: flex;
    gap: 1rem;
}

.navbar-icons svg {
    font-size: 1.5rem;
    color: white;
    transition: transform 0.3s ease, color 0.3s ease;
}

.navbar-icons svg:hover {
    transform: scale(1.2);
    color: #00bcd4;
}

/* Animation for smooth scrolling */
@keyframes slideDown {
    0% {
        transform: translateY(-20px);
        opacity: 0;
    }
    100% {
        transform: translateY(0);
        opacity: 1;
    }
}

.navbar {
    animation: slideDown 0.6s ease-out;
}

/* Mobile Responsive */
@media (max-width: 768px) {
    .navbar-links {
        flex-direction: column;
        gap: 1rem;
        align-items: center;
    }

    .navbar-icons {
        display: none;
    }

    .navbar-link {
        font-size: 1.3rem;
    }
}
